import { defineStore } from "pinia"
import { defaultAirIsland, defaultBoneIsland, defaultColdIsland, defaultEarthIsland, defaultFaerieIsland, defaultFireHavenIsland, defaultFireOasisIsland, defaultLightIsland, defaultPlantIsland, defaultPsychicIsland, defaultWaterIsland } from "./default-islands";
import { Island, MainState } from './element';



const mockIslandsState = (): Island[] => [
    { ...defaultPlantIsland() },
    { ...defaultColdIsland() },
    { ...defaultAirIsland() },
    { ...defaultWaterIsland() },
    { ...defaultEarthIsland() },
    { ...defaultFireHavenIsland() },
    { ...defaultFireOasisIsland() },
    { ...defaultLightIsland() },
    { ...defaultPsychicIsland() },
    { ...defaultFaerieIsland() },
    { ...defaultBoneIsland() },
]


export const useMainStore = defineStore('main', {
    state: (): MainState => {
        return {
          // all these properties will have their type inferred automatically
          islands: mockIslandsState()
        }
      },
})