export enum Element {
    Cold = "COLD",
    Fire = "FIRE",
    Water = "WATER",
    Earth = "EARTH",
    Plant = "PLANT",
    Air = "AIR",
    Light = "LIGHT",
    Psychic = "PSYCHIC",
    Faerie = "Faerie",
    Bone = "BONE",
    Ethereal = "ETHEREAL"
}

export interface MonsterElement {
    name: string,
    iconUrl: string
}

export interface Monster {
    name: string, 
    elements: Element[],
    breedingTimeInSeconds: number,
    breedingTimeInSecondsEnhanced: number
}

export interface MainState {
    islands: Island[]
}

export interface BreedingStation {
    upgraded: boolean
}

export interface NurseryStation { 
    upgraded: boolean
}

export interface Island {
    name: string,
    breedingStations: BreedingStation[],
    nurseryStations: NurseryStation[],
    availableMonsters: Monster[],
    availableElements: Element[]
}

