import { Element, Island } from "./element";

import { getMonstersByElements } from './utils';

export function defaultPlantIsland(): Island {
    const elements: Element[] = [
        Element.Plant,
        Element.Cold,
        Element.Water,
        Element.Earth
    ];

    const monsters = getMonstersByElements(elements);

    return {
        name: "Plant",
        ...defaultIslandState(),
        availableElements: elements,
        availableMonsters: monsters
    }
}


